		</view>
		<editor :id="editorId" show-img-size :read-only="isEdit" show-img-resize show-img-toolbar class="ql-container" :placeholder="placeholder" @statuschange="onStatusChange" @ready="onEditorReady" @input="eventhandle"></editor>
	</view>
</template>

<script>
	import Emitter from '@/uview-ui/libs/util/emitter.js';
	export default {
		name: 'fa-editor',
		mixins: [Emitter],
		props: {
			value: {
				type: String,
				default: ''
			},
			type: {
				type: String,
				default: 'html'
			},
			html: {
				type: String,
				default: ''
			}
		},
		data() {
			return {
				isEdit: false,
				placeholder: '开始输入...',
				editorId: this.$u.guid(10),
				formats: {}
			};
		},
		methods: {
			onStatusChange(e) {
				this.formats = e.detail;
			},
			onEditorReady() {
				let that = this;
				uni.createSelectorQuery()
					.in(this)
					.select(`#${that.editorId}`)
					.context(function(res) {
						that.editorCtx = res.context;
						//初始化数据
						if (that.html) {
							res.context.setContents({
								html: that.html,
								complete: function(e) {
									console.log(e);
								}
							});
						}
					})
					.exec();
			},
			toolBarClick(e) {
				let { name, value } = e.target.dataset;
				if (!name) return;
				this.editorCtx.format(name, value);
			},
			undo() {
				this.editorCtx.undo();
			},
			redo() {
				this.editorCtx.redo();
			},
			blur() {
				this.editorCtx.blur();
			},
			removeFormat() {
				this.editorCtx.removeFormat();
			},
			//插入横线
			insertDivider() {
				this.editorCtx.insertDivider({
					success: function() {
						console.log('insert divider success');
					}
				});
			},
			//插入时间
			insertDate() {
				const date = new Date();
				const formatDate = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`;
				this.editorCtx.insertText({
					text: formatDate
				});
			},
			clear() {
				this.editorCtx.clear({
					success: function(res) {
						console.log('clear success');
					}
				});
			},
			insertImage() {
				uni.chooseImage({
					count: 9,
					success: chooseImageRes => {
						const tempFilePaths = chooseImageRes.tempFilePaths;
						tempFilePaths.map(async item => {
							try {
								let res = await this.$api.goUpload({ filePath: item });
								this.editorCtx.insertImage({
									src: res.data.fullurl,
									data: {
										id: 'abcd',
										role: 'god'
									},
									width: '80%',
									success: function() {
										console.log('insert image success');
									}
								});
							} catch (e) {
								this.$u.toast('上传失败！');
							}
						});
					}
				});
			},
			getData() {
				return new Promise((resolve, reject) => {
					this.editorCtx.getContents({
						success: function(res) {
							res.html = res.html.replace(/wx:nodeid="\d+"/g,'');
							resolve(res);
						},
						fail: function(err) {
							reject(err);
						}
					});
				});
			},
			eventhandle(e) {
				console.log(e);
				let value = this.type == 'html' ? e.detail.html : e.detail.text;
				this.$emit('input', value);
				setTimeout(() => {
					this.dispatch('u-form-item', 'on-form-blur', value);
				}, 50);
			}
		}
	};
</script>

